.CatalogItem{display:flex;flex-direction:column;justify-content:space-between;width:calc(25% - 1.25rem);margin:.625rem;border-radius:.25rem}.CatalogItem__image{width:auto;max-width:13.75rem;max-height:13.75rem;margin:0 auto;padding-top:1.25rem}.CatalogItem__rate{margin-top:1rem;white-space:nowrap}.CatalogItem__rate.ant-rate{color:#ffb339}.CatalogItem__price{font-size:1.25rem;font-weight:400;margin-top:1rem;white-space:nowrap}.CatalogItem__bottom{display:flex;margin-top:1rem;transition:opacity .4s ease}.CatalogItem__count.ant-select{margin-left:1rem}@media (max-width:68.75rem){.CatalogItem{width:calc(33.33% - 1.25rem)}}@media (max-width:53.125rem){.CatalogItem{width:calc(50% - 1.25rem)}}@media (max-width:40.625rem){.CatalogItem{width:calc(100% - 1.25rem)}}