.Cart__count{margin-top:-1.5em;color:#aaa}.Cart__catalog-link{white-space:nowrap}.Cart__items{min-width:36.25rem;margin-top:2rem}.Cart__items td,.Cart__items th{padding:1rem 2rem}.Cart__items th{white-space:nowrap;border-radius:.5rem}.Cart__items thead tr{border-bottom:3px solid #ffb339}.Cart__items tbody tr:not(:last-child){border-bottom:1px solid #ccc}.Cart__header-sortable{cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;transition:background .4s ease}.Cart__header-sortable:hover{background:#f9f9f9}.Cart__header-order-icon{position:relative;top:.0625rem;margin-left:.375rem;color:#aaa}.Cart__header-order-icon_hidden{visibility:hidden}.Cart__submit{margin-top:3rem}@media (max-width:48rem){.Cart__items td,.Cart__items th{padding-right:1rem;padding-left:1rem}}@media (max-width:26.25rem){.Cart__items td,.Cart__items th{padding-right:.5rem;padding-left:.5rem}}