.ant-rate{font-family:Chinese Quote,-apple-system,BlinkMacSystemFont,Segoe UI,PingFang SC,Hiragino Sans GB,Microsoft YaHei,Helvetica Neue,Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;font-size:14px;-webkit-font-feature-settings:"tnum";font-feature-settings:"tnum";font-variant:tabular-nums;line-height:1.5;color:rgba(0,0,0,.65);box-sizing:border-box;line-height:unset;margin:0;padding:0;list-style:none;font-size:20px;display:inline-block;color:#fadb14;outline:none}.ant-rate-disabled .ant-rate-star{cursor:default}.ant-rate-disabled .ant-rate-star:hover{transform:scale(1)}.ant-rate-star{padding:0;display:inline-block;margin:0 8px 0 0;position:relative;transition:all .3s;color:inherit;cursor:pointer}.ant-rate-star:focus{outline:0}.ant-rate-star-first,.ant-rate-star-second{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;transition:all .3s;color:#e8e8e8}.ant-rate-star-first .anticon,.ant-rate-star-second .anticon{vertical-align:middle}.ant-rate-star:focus,.ant-rate-star:hover{transform:scale(1.1)}.ant-rate-star-first{position:absolute;left:0;top:0;width:50%;height:100%;overflow:hidden;opacity:0}.ant-rate-star-half .ant-rate-star-first,.ant-rate-star-half .ant-rate-star-second{opacity:1}.ant-rate-star-full .ant-rate-star-second,.ant-rate-star-half .ant-rate-star-first{color:inherit}.ant-rate-text{margin-left:8px;display:inline-block;font-size:14px}